
import React from 'react'
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Navbar from './components/Navbar';
import { HashRouter as Router, Route, Switch } from 'react-router-dom';
import Planets from './components/Planets';
import Planetsmain from './components/Planetsmain';
import Planetdisplay from './components/Planetdisplay';
import Planetcharacter from './components/Planetcharacter';
import Character12 from './components/Character12';






const App = () => {
    return (

        <Router>
            <Container className=" App p-5">
                <Row className="justify-content-center">

                    < Navbar/>
                  

                    <div className="everything"> 
                        <Switch> 
                            <Route exact path="/"> 
                                 < Planetdisplay />
                            </Route>
                            <Route exact path="/Planetcharacter"> 
                                 < Planetcharacter/>
                            </Route>
                            <Route exact path="/Planetsmain"> 
                                 < Planetsmain/>
                            </Route>
                            <Route exact path="/Character12"> 
                                 < Character12/>
                            </Route>
                            <Route exact path="/Planets"> 
                                 < Planets/>
                            </Route>
                        </Switch>
                    </div>

                </Row>
            </Container>
      </Router>
           
    )}

export default App
